  font-family: 'powel';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

/* @import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&family=Rye&display=swap'); */

:root {
  --aries: #ff010130;
  --taurus: #4b532040;
  --gemini: #5eff0040;
  --cancer: #559fbd40;
  --leo: #ffa60040;
  --virgo: #8f977940;
  --libra: #ffb6c140;
  --scorpio: #80000040;
  --sagittarus: #4169e140;
  --capricorn: #a54d2a40;
  --aquarius: #00ffff40;
  --pisces: #b19cd940;
}

html {
  box-sizing: border-box;
  font-family: 'powel';
}
/* 1. Use a more-intuitive box-sizing model */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* 2. Remove default margin */
* {
  margin: 0;
  padding: 0;
}

body {
  /* 3. Add accessible line-height */
  line-height: 1.5;
  /* 4. Improve text rendering */
  -webkit-font-smoothing: antialiased;

  /* font-family: 'Rye'; */
}

/* 5. Improve media defaults */
img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

/* 6. Inherit fonts for form controls */
input,
button,
textarea,
select {
  font: inherit;
}

/* 7. Avoid text overflows */
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

/* 8. Improve line wrapping */
p {
  text-wrap: pretty;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

/*
    9. Create a root stacking context
  */
#root,
#__next {
  isolation: isolate;
}
/* ---------------------------------------------------------- */
/* ---------------------------------------------------------- */
/* ---------------------------------------------------------- */

body {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  background-size: cover;

  /* display: flex;
  flex-direction: column;
  align-items: center; */

  /* width: 2000px; */
}
main {
  /* display: block; */
  /* justify-content: center; */
  /* left: 50%; */

  /* background-color: goldenrod; */
}

header {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.74);
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: center;

  /* background-image: url('/ressources/images/zwBgRec.png');
  background-size: cover; */

  /* box-shadow: inset rgba(160, 98, 98, 0.445) -8px 3px 8px 1px,
    inset rgba(156, 158, 145, 0.055) -11px -2px 5px 5px; */

  box-shadow: 0 0 3px white;

  /* border-radius: 0 0 10px 10px; */
  border: none;

  padding: 5px 10px 5px 10px;
  z-index: 5;

  user-select: none;
}

.headerLeft {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.headerMid {
  /* background-color: rgba(255, 0, 0, 0.616); */
  /* background-image: url('/ressources/images/logoRoundStar.png');
  background-size: cover; */
  display: flex;
  flex-direction: column;
}
header h1 {
  color: rgb(255, 255, 255);
  font-size: 4rem;
  font-weight: 900;
  text-align: center;
  grid-column: 2/3;

  text-shadow: 1px 1px 5px #ffffff, 2px 2px 5px #000000c4, 3px 3px 5px #ffffff88,
    4px 4px 5px #0000002f;
}
header h2 {
  color: rgb(255, 255, 255);
  font-size: 1.5rem;
  text-align: center;
}
@keyframes glow {
  0% {
    filter: brightness(1.2);
  }
  50% {
    filter: brightness(2.5);
  }
  100% {
    filter: brightness(1.2);
  }
}

.headerRight {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  /* padding-right: 20px; */
}

.factionImage {
  /* position: absolute; */
  /* width: 100%; */
  height: 50px;
  width: 50px;
  filter: brightness(1.2);
}

header button {
  /* width: 250px; */
  font-size: calc(2rem);
  text-shadow: 1px 1px #160303;
  text-wrap: nowrap;

  display: flex;
  gap: 10px;
  padding: 5px 30px;
  height: 80%;

  background-color: rgba(240, 248, 255, 0.836);
  box-shadow: inset rgba(255, 255, 255, 0.185) 3px 3px 4px 1px,
    inset black -2px -2px 6px 0px;
  border-radius: 10px;
  border: none;
}
header button:hover {
  /* filter: brightness(1.2); */
  /* animation: glow 1s ease-in-out infinite; */
  filter: brightness(1.5);
  cursor: pointer;
}

/* ---------------------------------------------------------------- */

#playGround {
  top: 150px;
  position: relative;
  width: 2000px;
  height: 2000px;

  /* background-color: yellowgreen; */
}
.universe {
  position: absolute;

  display: flex;
  flex-wrap: wrap;
  width: 2000px;
  height: 2000px;

  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
  background-size: cover;
}

/* ------------------------------------------- */
/* ------------------------------------------- */
/* ------------------------------------------- */
@media screen and (max-width: 1500px) {
  .header-button-text {
    display: none;
  }
  header h1 {
    font-size: calc(1rem + 2vw);
  }
  header h2 {
    font-size: calc(0.5rem + 1vw);
  }
}

@media screen and (min-width: 600px) {
  header {
    width: 100vw;
    /* left: 50%;
    transform: translate(-50%); */
  }
}

@media screen and (max-width: 600px) {
  /* header {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: red;

  } */
  header {
    display: none;
    grid-template-areas:
      'top top'
      'left right';
  }
  .headerMid {
    grid-area: top;
  }
  .headerLeft {
    grid-area: left;
    margin-right: 10px;
  }
  .headerRight {
    grid-area: right;
    margin-left: 10px;
  }
}
